<template>
  <div class="update-photo">
    <img :src="img" alt="" class="img" ref="img" />
    <div class="toolbar">
      <div class="cancel" @click="$emit('close')">取消</div>
      <div class="confirm" @click="onConfirm">完成</div>
    </div>
  </div>
</template>

<script>
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

import { updateUserPhoto } from '@/api/user.js'
export default {
  name: 'UpdatePhoto',

  props: {
    img: {
      type: [String, Object],
      required: true
    }
  },

  data () {
    return {
      cropper: null
    }
  },

  mounted () {
    const image = this.$refs.img
    this.cropper = new Cropper(image, {
      viewMode: 1,
      dragMode: 'move',
      aspectRatio: 1,
      autoCropArea: 1,
      cropBoxMovable: false,
      cropBoxResizable: false,
      background: false,
      movable: true
      // aspectRatio: 16 / 9,
      // crop (event) {
      //   console.log(event.detail.x)
      //   console.log(event.detail.y)
      //   console.log(event.detail.width)
      //   console.log(event.detail.height)
      //   console.log(event.detail.rotate)
      //   console.log(event.detail.scaleX)
      //   console.log(event.detail.scaleY)
      // }
    })

    // console.log(cropper)
  },

  methods: {
    async loadUserPhoto (blob) {
      this.$toast.loading({
        message: '更新中..',
        forbidClick: true,
        duration: 0
      })
      try {
        const formData = new FormData()
        // console.log(blob)
        formData.append('photo', blob)

        const { data: res } = await updateUserPhoto(formData)

        console.log(res)

        this.$emit('close')

        this.$emit('update-photo', res.data.photo)

        this.$toast.success('更新成功')

        // Pass the image file name as the third parameter if necessary.
        // formData.append('croppedImage', blob/*, 'example.png' */)

        // Use `jQuery.ajax` method for example
      } catch (err) {
        this.$toast('更新失败')
      }
    },

    onConfirm () {
      this.cropper.getCroppedCanvas().toBlob((blob) => {
        this.loadUserPhoto(blob)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.update-photo {
  .img {
    display: block;
    max-width: 100%;
  }
  background-color: #000;
  height: 100vh;
  .toolbar {
    display: flex;
    justify-content: space-between;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;

    .cancel,
    .confirm {
      width: 90px;
      height: 90px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #fff;
      font-size: 30px;
    }
  }
}
</style>
