<template>
  <div class="update-gender">
    <van-picker
  title="修改性别"
  show-toolbar
  :columns="columns"
  @confirm="onConfirm"
  @cancel="$emit('close')"
  @change="onChange"
  :default-index="value"
/>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user.js'
export default {
  name: 'updateGender',

  props: {
    value: {
      type: Number,
      required: true
    }
  },

  data () {
    return {
      columns: ['男', '女'],
      localGender: this.value
    }
  },

  mounted () {

  },

  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '更新中',
        forbidClick: true,
        duration: 0
      })

      try {
        const localGender = this.localGender
        // if (!localGender.length) {
        //   this.$toast('昵称不能为空')
        //   return
        // }

        await updateUserProfile({
          gender: localGender
        })

        this.$emit('input', localGender)

        this.$emit('close')

        this.$toast.success('更新成功')
      } catch (err) {
        this.$toast.fail('更新失败')
      }
    },
    onChange (picker, value, index) {
      this.localGender = index
    }
  }
}
</script>

<style lang="less" scoped>
  /deep/.van-picker__confirm{
    color: #3296fa;
  }

  /deep/.van-picker__cancel{
    color: #3296fa;
  }
</style>
