<template>
  <div class="update-birthday">
    <van-datetime-picker
  v-model="currentDate"
  type="date"
  title="选择年月日"
  :min-date="minDate"
  :max-date="maxDate"
  @cancel="$emit('close')"
  @confirm="onConfirm"
/>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user.js'
import dayjs from 'dayjs'
export default {
  name: 'updateBirthday',
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      minDate: new Date(1960, 0, 1),
      maxDate: new Date(),
      currentDate: new Date(this.value)
    }
  },

  mounted () {

  },

  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '更新中',
        forbidClick: true,
        duration: 0
      })

      try {
        const localBirthday = dayjs(this.currentDate).format('YYYY-MM-DD')
        // if (!localBirthday.length) {
        //   this.$toast('昵称不能为空')
        //   return
        // }

        await updateUserProfile({
          birthday: localBirthday
        })

        this.$emit('input', localBirthday)

        this.$emit('close')

        this.$toast.success('更新成功')
      } catch (err) {
        this.$toast.fail('更新失败')
      }
    }
  }
}
</script>

