<!-- eslint-disable vue/no-mutating-props -->
<template>
  <div class="updateName">
    <van-nav-bar
      title="编辑昵称"
      left-text="取消"
      right-text="完成"
      @click-right="onConfirm"
      @click-left="$emit('close')"
    >
    </van-nav-bar>

    <div class="filed-wrap">
      <van-field
      v-model="localName"
      rows="2"
      autosize
      type="textarea"
      maxlength="50"
      placeholder="请输入昵称"
      show-word-limit
    />
  </div>
    </div>

</template>

<script>
// 导入编辑个人信息的接口
import { updateUserProfile } from '@/api/user.js'
export default {
  name: 'updateName',

  props: {
    value: {
      type: String,
      required: true
    }
  },

  data () {
    return {
      localName: this.value
    }
  },

  mounted () {},

  methods: {
    async onConfirm () {
      this.$toast.loading({
        message: '更新中',
        forbidClick: true,
        duration: 0
      })

      try {
        const localName = this.localName
        if (!localName.length) {
          this.$toast('昵称不能为空')
          return
        }

        await updateUserProfile({
          name: localName
        })

        this.$emit('input', localName)

        this.$emit('close')

        this.$toast.success('更新成功')
      } catch (err) {
        this.$toast.fail('更新失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .filed-wrap{
    padding: 15px;

  }
</style>
