<template>
  <div class="UserProfile">
    <!-- 用户详情组件 -->
    <van-nav-bar
      title="个人信息"
      left-arrow
      @click-left="$router.back()"
      class="page-nav-bar"
    >
    </van-nav-bar>

    <input type="file" hidden ref="file" @change="onFileChange" />

    <van-cell title="头像" is-link @click="$refs.file.click()">
      <van-image round width="30" height="30" fit="cover" :src="user.photo" />
    </van-cell>
    <van-cell
      title="昵称"
      :value="user.name"
      is-link
      @click="isUpdateNameShow = true"
    ></van-cell>
    <van-cell
      title="性别"
      :value="user.gender | numToGender"
      is-link
      @click="isUpdateGenderShow = true"
    ></van-cell>
    <van-cell
      title="生日"
      :value="user.birthday"
      is-link
      @click="isUpdateBirthdayShow = true"
    ></van-cell>

    <!-- 弹出层区域--修改昵称 -->
    <van-popup
      v-model="isUpdateNameShow"
      position="bottom"
      :style="{ height: '100%', backgroundColor: '#f5f7f9' }"
    >
      <!-- hello -->
      <update-name
        v-if="isUpdateNameShow"
        @close="isUpdateNameShow = false"
        v-model="user.name"
      ></update-name>
    </van-popup>

    <!-- 修改性别 -->
    <van-popup
      v-model="isUpdateGenderShow"
      position="bottom"
      :style="{ backgroundColor: '#f5f7f9' }"
    >
      <!-- hello -->
      <update-gender
        v-if="isUpdateGenderShow"
        @close="isUpdateGenderShow = false"
        v-model="user.gender"
      ></update-gender>
    </van-popup>

    <!-- 修改生日 -->
    <van-popup
      v-model="isUpdateBirthdayShow"
      position="bottom"
      :style="{ backgroundColor: '#f5f7f9' }"
    >
      <!-- hello -->
      <update-birthday
        v-if="isUpdateBirthdayShow"
        @close="isUpdateBirthdayShow = false"
        v-model="user.birthday"
      ></update-birthday>
    </van-popup>

    <!-- 修改头像 -->
    <van-popup
      v-model="isUpdatePhotoShow"
      position="bottom"
      :style="{ height:'100%',backgroundColor: '#f5f7f9' }"
    >
      <!-- hello -->
      <update-photo
        v-if="isUpdatePhotoShow"
        @close="isUpdatePhotoShow = false"
        :img="img"
        @update-photo="user.photo=$event"
      ></update-photo>
    </van-popup>
  </div>
</template>

<script>
import { getUserProfile } from '@/api/user.js'
import updateName from '@/views/user-profile/components/update-name'
import updateGender from '@/views/user-profile/components/update-gender'
import updateBirthday from '@/views/user-profile/components/update-birthday'
import updatePhoto from '@/views/user-profile/components/update-photo'
export default {
  name: 'UserProfile',
  filters: {
    numToGender (val) {
      if (val === 0) {
        return '男'
      } else if (val === 1) {
        return '女'
      }
    }
  },
  components: {
    updateName,
    updateGender,
    updateBirthday,
    updatePhoto
  },

  data () {
    return {
      user: {},
      isUpdateNameShow: false,
      isUpdateGenderShow: false,
      isUpdateBirthdayShow: false,
      isUpdatePhotoShow: false,

      // 上传的头像
      img: null
    }
  },

  created () {
    this.loadUserProfile()
  },

  mounted () {},

  methods: {
    // 文件选择改变事件
    onFileChange () {
      // 获取文件对象
      const file = this.$refs.file.files[0]

      // 获取blob数据
      const data = window.URL.createObjectURL(file)

      console.log(data)

      this.img = data

      this.isUpdatePhotoShow = true

      // 如果每次选择同一个文件，第二次的change事件不会被触发，需要每次选完后将file的值清空
      this.$refs.file.value = ''
    },

    async loadUserProfile () {
      try {
        const { data: res } = await getUserProfile()

        console.log(res)

        this.user = res.data
      } catch (err) {
        this.$toast('获取用户信息失败')
      }
    }
  }
}
</script>

<style lang="less" scoped></style>
